<template>
  <div class="wrapperCenter">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>来粉统计 / 控制台 / 流量分析 / 实时访客</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search">
      <el-form :inline="true" :model="search" class="demo-form-inline" ref="search">
        <el-form-item label="地域">
          <el-select v-model="search.province_id" placeholder="地域">
            <el-option
              v-for="item in provinceArr"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="来源">
          <el-select v-model="search.channel_id" placeholder="请选择">
            <el-option v-for="item in channel" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="搜索词">
          <el-input v-model="search.search_keyword" placeholder="搜索词"></el-input>
        </el-form-item>
        <el-form-item label="IP地址">
          <el-input v-model="search.client_ip" placeholder="IP地址"></el-input>
        </el-form-item>
  <el-select v-model="search.site_id" placeholder="请选择网站"  class="searchDomain">
    <el-option
      v-for="item in domainArr"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>


        <el-form-item label="时间">
          <el-date-picker
            v-model="dateSpace"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="dateChange"
            value-format="yyyy-MM-dd HH:mm:ss"
            format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="searchFn">查询</el-button>
          <el-button type="reset" @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
</div>
      <el-table :data="tableData" tooltip-effect="dark" style="width: 100%" :header-cell-style="{background:'#F3F4F7',color:'#555'}">
        <el-table-column label="编号" prop="id">
          <!-- <template slot-scope="scope">{{ scope.row.date }}</template> -->
        </el-table-column>
        <el-table-column prop="domain_url" label="连接" width="120"></el-table-column>
        <el-table-column prop="put_channel" label="访问时间"></el-table-column>
        <el-table-column prop="change_type" label="地域/IP"></el-table-column>
        <el-table-column prop="change_child_name" label="来源"></el-table-column>
        <el-table-column prop="change_child_name" label="搜索词"></el-table-column>
        <el-table-column prop="change_child_name" label="入口标识"></el-table-column>
              <div slot="empty" class="nodata">
          <img src="@/assets/noHaveData.jpg" alt="" />
          <p>暂无数据</p>
      </div>
      </el-table>
      <el-pagination v-if="tableData.length > 0"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page.sync="search.page"
        :page-size="search.page_size"
        @current-change="currentChange"
      ></el-pagination>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: {
        province_id: "",
        channel_id: "",
        search_keyword: "",
        client_ip: "",
        page: 1,
        page_size: 10,
        start_time: "",
        end_time: "",
        site_id: ""
      },
      dateSpace: "",
      tableData: [],
      total: 0,
      channel: [],
      provinceArr: [],
      defaultProvId: "",
      domainArr: []
    };
  },
  mounted() {
    this.channel = JSON.parse(localStorage.getItem("channel"));
    this.search.channel_id = "1";
    this.axios.get("/v1/config/Region/list/city_type/1").then((res) => {
      if (res.data.code == 200) {
        this.provinceArr = res.data.result;
        this.search.province_id = res.data.result[0].id;
        this.defaultProvId = res.data.result[0].id;
        this.getData();
      }
    });

                this.domainArr = []
            this.axios.get("/v1/site_js/Site_list/index/page1/page_size/1000000").then(res => {
                if(res.data.code == 200){
                    res.data.result.list.forEach(e => {
                        let obj = {
                            value: e.id,
                            label: e.domain_url
                        }
                        this.domainArr.push(obj)
                    })
                }
            })


  },
  watch: {
    dateSpace(){
      this.search.start_time = this.dateSpace ? this.dateSpace[0] : "";
      this.search.start_time = this.dateSpace ? this.dateSpace[1] : "";
    }
  },
  methods: {
    
    searchFn(){
      this.search.page = 1;
      this.getData()
    },
    currentChange(e) {
      this.search.page = e;
      this.getData();
    },
    reset() {
      this.search = {
        province_id: this.defaultProvId,
        channel_id: "1",
        search_keyword: "",
        client_ip: "",
        page: 1,
        page_size: 10,
        start_time: "",
        end_time: "",
      }
    },
    dateChange(a, b) {
      this.search.start_time = a ? a[0] : "";
      this.search.end_time = a ? a[1] : "";
    },
    getData(params) {
      this.axios
        .post("/v1/data/Real_time_data/index", params ? params : this.search)
        .then((res) => {
        });
    },
  },
};
</script>

<style lang="less" scoped>
.searchDomain{
  margin-left: 0;
  margin-right: 20px;
}
</style>